@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text:wght@400;600&display=swap);
html, body {
  font-size: 62.5%;
  height: 100%;
  margin: 0;
  scroll-behavior: smooth;
  font-family: 'STIX Two Text', serif;
}

h1 {
  font-size: 6rem;
  font-family: 'STIX Two Text', serif;
}

h2 {
  font-size: 4rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.5rem;
}

p {
  font-size: 1.8rem;
  margin: 0;
  line-height: 130%;
}

a {
  font-size: 2rem;
}

hr {
  width: 80%;
}

@media screen and (max-width: 480px) {
  h2 {
    font-size: 5rem;
  }
}

#home-description-container{box-sizing:border-box;padding:10rem 1rem;height:100vh;background-image:url(/portfolio/static/media/background.e88cac6e.png);display:flex;justify-content:center;align-items:center}#home-aboutme-container{box-sizing:border-box;padding:10rem 13rem;width:100%;display:flex;justify-content:space-around}#home-techno-container{box-sizing:border-box;padding:3rem 5rem;width:100%;background-color:#fff;display:flex;justify-content:space-around;align-items:center}#home-project-container{box-sizing:border-box;padding:10rem 13rem;background-color:#8d99ae;display:flex;flex-direction:column;align-items:center}#footer-container{box-sizing:border-box;padding:10rem 13rem;height:30vh;display:flex;align-items:center}@media screen and (max-width: 480px){#home-description-container{padding:2rem;flex-direction:column;align-content:center}#home-page-heading{font-size:6rem;margin:2rem 0}#home-aboutme-container{padding:2rem}}
#heading-container{display:flex;flex-direction:column;align-items:center}#heading-text{color:#edf2f4;text-align:center}#heading-name{color:#ef233c}@media screen and (max-width: 480px){#home-page-picture{margin:2rem 0;height:40rem;width:30rem}}
#aboutMe-content-container{margin:0;display:flex;flex-direction:column;align-items:center}#aboutMe-heading{margin:1.5rem 0}#portfolioPic{height:25rem;border-radius:3rem;margin:2rem 0}#aboutMe-para-container{box-sizing:border-box;text-align:center}#aboutMe-content{font-size:2rem;line-height:130%;white-space:pre-wrap}
#text-box{box-sizing:border-box;height:100%;width:100%;padding:1rem 2rem;background-color:#edf2f4;display:flex;flex-direction:column;align-items:center}@media screen and (max-width: 480px){#text-box{border-radius:4rem;height:auto;width:100%}}
#technologies-lists-container{box-sizing:border-box;padding:2rem;text-align:center}#tech-heading{margin:2rem;margin-bottom:1rem}#hr-line{margin-bottom:2rem}#tech-text{font-size:2rem;margin-top:1rem}
#projects-heading{color:#fff;margin-bottom:2rem}#projects-container{display:flex;justify-content:space-between}#project{box-sizing:border-box;max-width:60rem;padding:2rem;display:flex;flex-direction:column;align-items:center}#image-container{height:25rem;margin-bottom:3rem}#project-image{height:100%;border:4px solid #edf2f4;transition:-webkit-transform .5s ease;transition:transform .5s ease;transition:transform .5s ease, -webkit-transform .5s ease}#project-image:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#project-heading{display:inline;margin-bottom:1.5rem}#project-content{font-size:2rem}
#link-component-container{width:100%;display:flex;justify-content:space-around}
#links-container{display:flex;flex-direction:column;align-items:center}#link-text{margin-bottom:.3em;text-decoration:none}
